* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: darkblue;
  color: white;
}

.movie_container {
  display: flex;
  flex-wrap: wrap;
}

.movie {
  position: relative;
  width: 250px;
  margin: 1rem;
  background-color: rgb(1, 1, 255);
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.2);
  overflow: hidden;

}

.movie img {
  max-width: 100%;
}

.movie-info {
  display: flex;
  padding: 1.2rem;
  align-items: center;
  justify-content: space-between;
}

.movie-info h3 {
  margin: 0;
}

.movie-overview {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  color: black;
  max-height: 100%;
  overflow: hidden;
  padding: 1rem;
  transform: translateY(100%);
  transition: transform 0.3s ease-in-out;
}

.movie:hover .movie-overview{
  transform: translateY(0%);
}

header {
  background-color: darkblue;
  display: flex;
  padding: 1rem;
  justify-content: flex-end;
}

.search {
  font-family: inherit;
  font-size: 1rem;
  border: 2px solid blue;
  border-radius: 50px;
  background-color: whtie;
  padding: .5rem 1rem;

}

.root {
  display: flex;
  flex-direction: column;
}

.rating {
  background-color: white;
  color: black;
  padding: .2rem;
  border-radius: 5px;
  font-weight: bold;
}